body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.board{
  position: relative;
}

.status {
  padding-bottom: 20px;
  font-size: 70px;
}

.status h1 {
  color: coral;
}

.col-one {
  height: 100px;
  position: relative;
}

.col-one button{
  width: 100px;
  height: 100px;
  border: 1px solid black;
  font-size: 50px;
  font-weight: bold;
}

.button:active {
  position: absolute;
  top:1px;
}

.col-one button:hover{
  cursor: pointer;
}

.col-one button:focus {
  outline: none;
}

.move-bold{
  font-weight: bold;
}

.red{
  color:red;
}
